// ---------------------------------------------------------------------
//  Base Global Styles
// ---------------------------------------------------------------------

html,
body {
  background: white;
  font-size: 62.5%;
}

body,
input,
button,
select,
textarea {
  font-family: $main-font;
  font-size: $base-font-size;
  line-height: $base-line-height;
  letter-spacing: -0.01em;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  color: $black;


  @include start-at($medium) {
    font-size: $base-font-size;
  }
}

main#primary {
  min-height: calc(100vh - 146px);
}

.grid {
  display: flex;
  flex-wrap: wrap;
}

a {
  color: inherit;
  text-decoration: none;

  &:hover {
    cursor: pointer;

  }

  &[href^="tel:"] {

    &:hover {
      cursor: default;
    }
  }
}

[data-scroll-to],
[data-click-target],
[data-click] {
  cursor: pointer;
}

img,
video {
  height: auto;
  max-width: 100%;
  display: block;
}
figure,
img {
  margin: 0;
  padding: 0;
}

sup {
  vertical-align: super;
  font-size: smaller;
}

picture > * {
  width: 100%;
  height: auto;
}

@mixin selection {
  color: $white;
  background: $blue;
}

::selection {
  @include selection;
}
::-moz-selection {
  @include selection;
}

hr {
  margin: 0;
  padding: 0;
  border: none;
  border-top: 1px solid $border;
}

address {
  font-style: normal !important;
}

i {
  display: block;
}


